@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/open-sans-400.woff2") format("woff2"), url("../fonts/open-sans-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/open-sans-600.woff2") format("woff2"), url("../fonts/open-sans-600.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
@media (min-width: 768px) {
  html {
    font-size: 18px;
  }
}

body {
  width: 100%;
  min-width: 320px;
  height: 100%;
  background-color: #ffffff;
}

section {
  margin: 24px 0;
}
@media (min-width: 375px) {
  section {
    margin: 30px 0;
  }
}
@media (min-width: 768px) {
  section {
    margin: 40px 0;
  }
}
@media (min-width: 1024px) {
  section {
    margin: 60px 0;
  }
}

a {
  color: inherit;
  text-decoration: none;
  transition: color 0.3s ease;
}
@media (hover: hover) {
  a:hover:not(.focus-visible) {
    color: #7553ff;
  }
}
a.focus-visible:focus {
  color: #7553ff;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 16px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 24px;
  }
}
@media (min-width: 1024px) {
  .container {
    padding: 0 32px;
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  min-width: 140px;
  padding: 11px 23px;
  font-size: 14px;
  line-height: 20px;
  color: #404040;
  background-color: #fed74b;
  border: 1px solid #cba500;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btn--restyled {
  border-radius: 10px;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    color: inherit;
    background-color: #cba500;
  }
}
.btn.focus-visible:focus {
  color: inherit;
  background-color: #cba500;
}
@media (min-width: 375px) {
  .btn {
    min-width: 165px;
  }
  .btn--restyled {
    min-width: 190px;
  }
}
@media (min-width: 1024px) {
  .btn {
    min-width: 190px;
    padding: 15px 31px;
    font-size: 18px;
  }
  .btn--restyled {
    min-width: 230px;
  }
}

.header {
  padding: 20px 0;
  background-color: #fed74b;
}

.footer {
  padding: 20px 0;
  background-color: #fed74b;
}

.header__container {
  display: flex;
  align-items: center;
}

.header__logo:not(:last-child) {
  margin-right: 30px;
}
@media (min-width: 375px) {
  .header__logo:not(:last-child) {
    margin-right: 40px;
  }
}
@media (min-width: 768px) {
  .header__logo:not(:last-child) {
    margin-right: 60px;
  }
}

.header__logo-img {
  width: 100px;
  height: auto;
}
@media (min-width: 768px) {
  .header__logo-img {
    width: 130px;
  }
}

.offers__list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 24px 16px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 375px) {
  .offers__list {
    gap: 30px 16px;
  }
}
@media (min-width: 768px) {
  .offers__list {
    grid-template-columns: repeat(2, 1fr);
    gap: 40px 24px;
  }
}
@media (min-width: 1024px) {
  .offers__list {
    grid-template-columns: 1fr;
    gap: 60px 32px;
  }
}

.offer-card {
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1px solid #828282;
  border-radius: 16px;
  overflow: hidden;
}
@media (min-width: 1024px) {
  .offer-card {
    flex-direction: row;
    max-width: 1110px;
    margin: 0 auto;
  }
}

.offer-card__cover {
  position: relative;
  flex-shrink: 0;
  width: 100%;
  padding-top: 64.7058823529%;
}
.offer-card--restyled .offer-card__cover {
  padding-top: 82.3529411765%;
}
@media (min-width: 1024px) {
  .offer-card__cover {
    flex-shrink: 0;
    width: 470px;
    padding-top: 0;
  }
  .offer-card--restyled .offer-card__cover {
    padding-top: 0;
  }
}

.offer-card__badge {
  position: absolute;
  top: 40px;
  left: 0;
  z-index: 3;
  padding: 8px 16px;
  color: #ffffff;
  background-color: #099ce8;
}
.offer-card--restyled .offer-card__badge {
  top: 0;
  background-color: #7553ff;
  border-bottom-right-radius: 12px;
}
@media (min-width: 768px) {
  .offer-card__badge {
    top: 50px;
    padding: 12px 20px;
  }
  .offer-card--restyled .offer-card__badge {
    top: 0;
  }
}
@media (min-width: 1024px) {
  .offer-card__badge {
    top: 60px;
    padding: 12px 32px;
  }
  .offer-card--restyled .offer-card__badge {
    top: 0;
  }
}

.offer-card__cover-img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.offer-card__content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 16px 12px 18px;
}
@media (min-width: 1024px) {
  .offer-card__content {
    padding: 16px 20px 26px;
  }
}

.offer-card__title {
  margin: 0;
  font-weight: 600;
  font-size: 14px;
  line-height: 22px;
}
.offer-card__title:not(:last-child) {
  margin-bottom: 20px;
}
.offer-card__title strong {
  font-weight: 600;
  text-transform: uppercase;
}
@media (min-width: 1024px) {
  .offer-card__title {
    font-weight: 400;
    font-size: 26px;
    line-height: 34px;
    color: #000000;
  }
  .offer-card__title:not(:last-child) {
    margin-bottom: 11px;
  }
}

.offer-card__duration {
  order: -1;
  display: flex;
  align-items: center;
  font-size: 12px;
  color: #9e9e9e;
}
.offer-card__duration:not(:last-child) {
  margin-bottom: 10px;
}
@media (min-width: 1024px) {
  .offer-card__duration {
    order: unset;
  }
}

.offer-card__duration-icon {
  flex-shrink: 0;
  width: 16px;
  height: 16px;
  margin-right: 9px;
}

.offer-card__features {
  margin: 0;
  padding: 0;
  color: #4b4b4b;
  list-style: none;
}
.offer-card__features:not(:last-child) {
  margin-bottom: 35px;
}
@media (min-width: 1024px) {
  .offer-card__features {
    font-size: 18px;
    color: #343434;
  }
  .offer-card__features:not(:last-child) {
    margin-bottom: 25px;
  }
}

.offer-card__feature {
  display: flex;
  align-items: center;
}
.offer-card__feature:not(:last-child) {
  margin-bottom: 9px;
}
@media (min-width: 1024px) {
  .offer-card__feature:not(:last-child) {
    margin-bottom: 11px;
  }
}

.offer-card__feature-icon {
  flex-shrink: 0;
  align-self: flex-start;
  width: 16px;
  height: 16px;
  margin-top: 2px;
  margin-right: 20px;
  color: #fecf01;
}

.offer-card__departures {
  width: 100%;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .offer-card__departures {
    flex-direction: row;
  }
}

.offer-card__departures-title {
  flex-shrink: 0;
}
.offer-card__departures-title:not(:last-child) {
  margin-bottom: 7px;
}
@media (min-width: 1024px) {
  .offer-card__departures-title:not(:last-child) {
    margin-top: 3px;
    margin-right: 9px;
    margin-bottom: 0;
  }
}

.offer-card__departure-list {
  flex-grow: 1;
  display: flex;
  flex-wrap: wrap;
  max-width: 100%;
  margin: 0 -3px;
  padding: 0;
  list-style: none;
}

.offer-card__departure-item {
  flex-shrink: 0;
  margin: 0 3px;
}

.offer-card__departure-link {
  display: block;
  padding: 6px 12px;
  background-color: #d9e8ff;
  border-radius: 11px;
  transition: color 0.3s ease, background-color 0.3s ease, box-shadow 0.3s ease;
}
@media (hover: hover) {
  .offer-card__departure-link:hover:not(.focus-visible) {
    color: #ffffff;
    background-color: #6ba6ff;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  }
}
.offer-card__departure-link.focus-visible:focus {
  color: #ffffff;
  background-color: #6ba6ff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
}
@media (min-width: 1024px) {
  .offer-card__departure-link {
    font-size: 18px;
    padding: 6px 16px;
  }
}

.offer-card__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
}
@media (min-width: 1024px) {
  .offer-card__footer {
    justify-content: flex-start;
  }
}

.offer-card__price {
  margin: 0;
  font-size: 30px;
  line-height: 1;
}
.offer-card__price:not(:last-child) {
  margin-bottom: 4px;
}
@media (min-width: 1024px) {
  .offer-card__price {
    font-size: 36px;
  }
}

.offer-card__hot-price {
  max-width: 130px;
  margin: 0;
  font-size: 10px;
  line-height: 12px;
}
.offer-card__hot-price:not(:last-child) {
  margin-bottom: 4px;
}
@media (min-width: 1024px) {
  .offer-card__hot-price {
    max-width: 160px;
    font-size: 14px;
    line-height: 16px;
  }
}

.offer-card__details-btn {
  align-self: flex-start;
  margin-left: 9px;
}
@media (min-width: 375px) {
  .offer-card__details-btn {
    margin-left: 15px;
  }
}

